<template>
    <div class="bind-id">
        <nav-bar
            title="换绑身份证"
            :click-back-handler="clickBackHandler"
            :finish-execute="true" />
        <div class="content">
            <div class="item">
                <div class="label">真实姓名</div>
                <input
                    type="text"
                    class="input"
                    ref="nameInput"
                    v-model="userName">
            </div>
            <div class="item">
                <div class="label">身份证号</div>
                <input
                    type="tel"
                    class="input"
                    v-model="userId">
            </div>

            <div class="hint">
                <div class="hint__text">
                    <span>*</span> 身份证信息须与微信实名信息一致<span>（1年限换绑1次）</span>
                </div>
                <div
                    class="hint__link"
                    @click="$router.push('bindexample')">
                    如何查看微信实名？
                </div>
            </div>
        </div>
        <div
            :class="{
                button: true,
                clickable: isClickable,
            }"
            @click="clickButton">
            确定换绑
        </div>
    </div>
</template>

<script>
import debounce from 'lodash.debounce';
import config from '@/config';
import NavBar from '@/components/NavBar';
import { bindId } from '@/api/withdrawAuth';
import {
    toast,
    quitCurrentWebview,
    isTakeOverBackByWeb,
    setSwipeBackStatus,
} from '@/utils/jsApiV2/client';
import { addCustomEventListener, removeCustomEventListener } from '@/utils/event';
import { set, get } from '@/utils/cache';
import { isSameDay } from '@/utils/time';

export default {
    name: 'BindId',
    components: {
        NavBar,
    },
    data() {
        return {
            userName: '',
            userId: '',
            bindMsgKey: 'bindMsg',
        };
    },
    computed: {
        isClickable() {
            return this.userName && this.userId;
        },
    },
    methods: {
        focusInput() {
            this.$refs.nameInput.focus();
        },
        getBindMsg() {
            return JSON.parse(get(this.bindMsgKey) || '{}');
        },
        setBindMsg() {
            const now = new Date();
            const msg = this.getBindMsg();
            const time = new Date(msg.time || 0);

            if (isSameDay(now, time)) {
                msg.num += 1;
            } else {
                msg.num = 1;
            }

            msg.time = now;

            set(this.bindMsgKey, JSON.stringify(msg));
        },
        getBindNum() {
            const now = new Date();
            const msg = this.getBindMsg();
            const time = new Date(msg.time || 0);

            if (isSameDay(now, time)) {
                return msg.num;
            }

            return 0;
        },
        clickBackHandler() {
            this.$router.go(-1);
        },
        clickButton: debounce(function handler() {
            const num = this.getBindNum();

            if (this.isClickable) {
                if (num < 5) {
                    this.setBindMsg();

                    bindId({
                        realName: this.userName,
                        cardNo: this.userId,
                    }).then(() => {
                        this.$o2o({
                            e_c: '提现',
                            e_a: '成功',
                            e_n: '身份证换绑成功',
                        });

                        toast('换绑成功，马上提现');
                        quitCurrentWebview();
                    }).catch((e) => {
                        const msg = e.msg || '网络异常，请稍后重试';
                        toast(msg);
                    });
                } else {
                    toast('输入错误次数过多，明天再试哈');
                }
            }
        }, config.debounceWait),
    },
    created() {
        this.$root.hidePlaceholder();
        this.$o2o({
            e_c: '提现',
            e_a: '曝光',
            e_n: '身份证换绑页面曝光',
        });
    },
    mounted() {
        // 禁止左滑
        setSwipeBackStatus('false');
        // 处理返回
        isTakeOverBackByWeb(true);
        addCustomEventListener('backKey', this.clickBackHandler);
        // 默认focus
        this.focusInput();
    },
    destroyed() {
        setSwipeBackStatus('true');
        isTakeOverBackByWeb(false);
        removeCustomEventListener('backKey', this.clickBackHandler);
    },
};
</script>

