import request, { apiResError } from './request';

/**
 * https://wiki.n.miui.com/pages/viewpage.action?pageId=132816456
 * 通用参数 && 用户登录相关 cookie
 */
export function get(data = {}) {
    return request.get('/api/v1/identity/authinfo', data)
        .then((response) => {
            if (response.data.status !== 200) {
                apiResError(response);

                return Promise.reject(response.data);
            }

            return Promise.resolve(response.data.data);
        });
}

/**
 * https://wiki.n.miui.com/pages/viewpage.action?pageId=132816456
 * 通用参数 && { realName: '', cardNo: '' } && 用户登录相关 cookie
 */
export function post(data = {}) {
    return request.post('/api/v1/identity/auth', data)
        .then((response) => {
            if (response.data.status !== 200) {
                return Promise.reject(response.data);
            }

            return Promise.resolve();
        });
}

